import React from "react";
import MainLayout from "../layouts/MainLayout";

export default function TacGia() {
  return (
    <MainLayout>
      <h2>Thông tin tác giả</h2>
      <ul>
        <li>
          LingYun khởi đầu game TLBB ở máy chủ Bát Quái Chưởng (nhân vật 4x) - ở
          cái thời mà VLTK, VLTK2, Phong Thần, Cửu Long Tranh Bá còn rần rần.
          Tuy nhiên anh ấy không có nhiều thời gian, mặc dù với "kinh nghiệm"
          chơi qua các tựa game MMO, anh ấy vẫn nhớ TLBB là tựa game hay hơn cả.
          Về sau anh ấy cũng chơi lại (lại một nhân vật 4x) ở Chính Khí Hạo
          Nhiên nhưng sau đó cũng bỏ ngang. Sau này thêm nữa, anh lại chơi tiếp
          một nhân vật 4x ở Cửu Dương Thần Công, tham gia nhắn tin SMS nhận code
          Thiên Long Kim Tôn, acc có điểm tặng, quà tặng Bí Ngân, Vải Bông.
          Nhưng sau đó cũng bỏ ngang.
        </li>
        Mãi đến vài năm sau, có thời gian chăm chút hơn, và nhớ về niềm đam mê
        của mình với TLBB ngày xưa, anh ấy cố gắng tìm và quay lại nhân vật 4x
        cũ ở Cửu Dương Thần Công với tên nhân vật đã được đặt từ lâu: LingYun.
        Thời gian mới chơi lại này cũng là lúc các máy chủ cũ khá bão hòa,
        LingYun tự chơi một mình, tự luyện công trên Yến Vương Cổ Mộ, nhặt rương
        Hoa Sơn, luyện đan Kính Hồ, dần dần anh cũng tìm được vài người tiền bối
        để giúp đỡ trên bước đường tu tập.
        <li>
          LingYun hay theo dõi các video PK của boyvip5 (Chu Tiểu Long),
          iGoogle, Phiêu Phong, ngưỡng mộ họ và thầm mong một ngày nào đó mình
          cũng tìm được chỗ đứng của riêng mình trên giới giang hồ. Từ những
          tháng ngày đi phụ bản nhặt ngọc 1 hợp thành 4 với mục tiêu acc full
          ngọc 4, LingYun cũng khá tự hào về sự cố gắng của mình, nhưng anh biết
          một thực tế "không bỏ tiền, thì cũng bỏ công", và về sau này LingYun
          chịu khó đầu tư hơn, vì vốn dĩ anh rất tâm huyết, vui khổ cùng acc, tự
          bỏ tiền ngu ra để trải nghiệm và làm acc cho chính mình.
        </li>
        Năm 2015 anh chấp bút chuyên trang Thiên Long Bát Bộ Cẩm Nang Tạp Lục
        (tlbb.readthedocs.io) nhằm ghi chép những kinh nghiệm, kiến thức, hiểu
        biết của riêng mình về các khía cạnh của TLBB, với nguyện vọng giúp cho
        mình (thỏa đam mê), giúp cho đời, giúp cho đồng đạo võ lâm gần xa.
        <li>
          Kênh Youtube click2teen của LingYun cũng dần dần được biết đến hơn,
          khởi đầu là những trận lôi đài còn non nớt, nhưng LingYun cũng cố gắng
          đăng tải để cho mọi người thấy (chê cười), vì anh biết không có gì là
          hoàn hảo cả, cái cốt là biết nhìn nhận, chia sẻ, học hỏi, tiếp thu.
          Lối đánh của LingYun không hoa mĩ như các vị tiền bối, mà chỉ có 4 chữ
          "tùy cơ ứng biến", pha chút ngẫu hứng chứ không bài vở, chặt chẽ và
          chu toàn được, vì đơn giản LingYun cũng là một kẻ phàm phu tục tử, vốn
          thích những điều giản dị và tự nhiên. Bên cạnh đó, anh còn phát biểu ý
          kiến, chia sẻ, phân tích về cách tự xử các phụ bản, hoạt động, chức
          năng in-game.
        </li>
      </ul>
      Hôm nay, LingYun tái bút vào ngày 11/2/2018 - nhân dịp anh đăng tải bộ web
      toolkit với một số tiện tích liên quan đến game TLBB mà anh hằng đam mê.
      <hr />
      <ul>
        <li>
          <a href="https://youtube.com/click2teen">YouTube - click2teen</a>
        </li>
        <li>
          <a href="https://facebook.com/chiase.tlbb">
            Fanpage Thiên Long Bát Bộ Cẩm Nang Tạp Lục
          </a>
        </li>
        <li>
          <a href="https://tlbb.github.io">
            Thiên Long Bát Bộ Cẩm Nang Tạp Lục - Quyển Thượng
          </a>
        </li>
        <li>
          <a href="https://tlbb.readthedocs.io">
            Thiên Long Bát Bộ Cẩm Nang Tạp Lục - Quyển Hạ
          </a>
        </li>

        <li>
          <a href="https://nghiaht.github.io/tlbb-web-toolkit">
            TLBB Free Tools - Top công cụ hỗ trợ làm acc TLBB
          </a>
        </li>
      </ul>
    </MainLayout>
  );
}
